  box-sizing: border-box;
}

html {
  font-family: 'Poppins' sans-serif;
}

body {
  margin: 0;
}
#photo-main {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-color: rgba(0, 0, 0, 0.5);
  background-blend-mode: color;
  padding-top: 150px;
  display: flex;
  flex-direction: column;
}

#navBar-container {
  background-color: aquamarine;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 8vh;
  box-shadow: 0 0 5px rgb(0, 0, 0);
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

img {
  object-fit: cover;
}

figure {
  padding: 0;
  margin: 0;
}

#navBar {
  display: flex;
  justify-content: space-between;
}
span {
  font-family: cursive;
  font-size: x-large;
  padding: 4px;
}
#nav-icons {
  display: flex;
  justify-content: space-between;
}
.card-img {
  width: 73%;
  margin-left: 13%;
}

.icon-img {
  width: 2rem;
  margin: 3px;
  margin-right: 17px;
}
.img-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: antiquewhite;
  width: 72%;
  padding-top: 5%;
  border-radius: 7px;
}
.img-a {
  color: rgb(0, 0, 0);
  font-size: larger;
  padding-bottom: 3%;
  float: none;
  font: 500 24px / 30px 'cursive';
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: top;
  position: relative;
}
.content .title a {
  position: relative;
}
.img-details a::after {
  background: #a97345;
  position: absolute;
  left: 0;
  right: 0;
  top: auto;
  bottom: 0;
  margin: 0 auto;
  content: '';
  height: 2px;
  width: 30px;
  margin-bottom: 7px;
}

.img-figure {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10%;
}
#photo-section {
  display: flex;
  flex-direction: column;
  padding: 20px;
  align-self: center;
}
.product-card {
}

